                "id": "", "type": "chat", from: "", "to": this.forName, "data": "诊疗意见", "ext": { orderId: this.toAndfrm, appRecordId: this.toAndfrm }, "error": false, "errorText": "",
                "errorCode": "",
                "timeStamp": 1499072478613,
                "left": true,
                status: "text",
                toAndfrm: this.toAndfrm,
              };
              var id = conn.getUniqueId();                 // 生成本地消息id
              var msg = new WebIM.message('txt', id);      // 创建文本消息
              msg.set({
                msg: texts.data,                  // 消息内容
