                ext: { orderId: this.toAndfrm, appRecordId: this.toAndfrm }, // 接收消息对象（用户id）
                roomType: false,
                success: function(id, serverMsgId) {
                  that.savMasge(texts);//保存历史记录
                  //推到vuex 拼接聊天消息列表
                  that.$store.commit('pushMasge', texts);
                }
              });
              msg.body.chatType = 'singleChat';
              conn.send(msg.body);
              this.dialogVisible = false;
              this.Visible = false
            }).catch(function(res) {
              this.warning("诊疗意见书发送失败，请稍后重试")
            })
          } else {
            return false;
          }
      })
