    //订单详情
    oder: function(res) {
      //初始化订单详情
      var expertChart = {
        imageUrl: defaultImg,//默认头像
        startTime: "YYY-MM-DD 00:00:00",
        name: "————",
        title: "————",
        doctorId: null,
        hospitalName:"————",
        expertId: null,
        expertName: null,
        type:null,
        userId: null,
        clinicsNo: null,
        pbStartTime :{ ymd: "", hms: "" },
        pbEndTime :{ ymd: "", hms: "" },
        sex:"————",
        status:res.status
      };
      //清空聊天对象
      this.toAndfrm=null;
      //清空右边数据
      if(res==-100){
        this.epxertsChart = expertChart;
        this.toAndfrm=null;
        this.expertChertByOdering();
        this.masageList=[];
        this.epxertsChart.sex="————";
        return false;
      }
      if (res.status) {
        //保存聊天订单id
        this.toking = res.id;
        //保存订单id
        this.toAndfrm=res.id;
        //专家医生头像和环信账号
        this.expertURL = res.expertImageUrl;
        this.docterURL = res.doctorImageUrl;
        if(this.user.userType==2){
          this.expertURL=res.doctorImageUrl;
          this.docterURL = res.expertImageUrl;
        }
        if(!this.expertURL){
          this.expertURL=defaultImg
        }
        if(!this.docterURL){
          this.docterURL=defaultImg
        }
        this.docterHx = res.doctorEasemob;
        this.expertHx = res.expertEasemob;
        //是否存在排班时间（订单预约时间）
        if (res.pbStartTime) {
          expertChart.pbStartTime = publicFn.publicLo.timeCheng(res.pbStartTime);
        }
        if (res.pbEndTime) {
          expertChart.pbEndTime = publicFn.publicLo.timeCheng(res.pbEndTime);
        }
        //是否存在结束时间（订单协诊结束时间）
        if (res.endTime) {
          expertChart.endTime = publicFn.publicLo.timeCheng(res.endTime);
          expertChart.endTime = expertChart.endTime.hms;
        }
        if (this.users) {
          expertChart.doctorId = res.doctorId;
          expertChart.expertId = res.expertId;
          expertChart.expertName = res.expertName;
          expertChart.userId = res.patientId;
          expertChart.type = res.type;
          expertChart.clinicsNo = res.orderNo;
          expertChart.startTime = publicFn.publicLo.timeCheng(res.startTime);
          expertChart.startTime = expertChart.startTime.times;
          expertChart.status=res.status;
          expertChart.patientName=res.patientName;
          expertChart.sex=res.sex;
          expertChart.orderNo=res.orderNo;
          expertChart.age=res.age;
          expertChart.doctorName=res.doctorName;
          //匹配数据库字典疾病类型
          for(let k in this.recordTypeList){
            if(this.recordTypeList[k].id==res.recordType){
              expertChart.recordType=this.recordTypeList[k].text;
            }
          }
          //当前用户为专家角色时
          if (this.users.userType == 2) {
            this.forName = this.docterHx;//聊天环信账号（对方）
            expertChart.name = res.doctorName;//渲染名称（）
            expertChart.title = res.doctorTitle;
            //没有图片给默认图片
            if(res.doctorImageUrl){
              expertChart.imageUrl = res.doctorImageUrl;
            }else {
              expertChart.imageUrl=defaultImg
            }
            this.expertChertByOdering(res.doctorId)
          }
          //当前用户为医生角色时
          if (this.users.userType == 1) {
            this.forName = this.expertHx;
            expertChart.name = res.expertName;
            expertChart.title = res.expertTitle;
            if(res.expertImageUrl){
              expertChart.imageUrl = res.expertImageUrl;
            }else {
              expertChart.imageUrl=defaultImg
            }
            this.expertChertByOdering(res.expertId)
          }
          if(res.type==1){
            expertChart.typeWay="视频面诊"
          }else {
            expertChart.typeWay="图文问诊"
          }
          this.status = res.status;
          if(res.status==3){
            var expertChart = {
              imageUrl: defaultImg,//默认头像
              startTime: "YYY-MM-DD 00:00:00",
              name: "———— ",
              title: "———— ",
              doctorId: null,
              hospitalName:"———— ",
              expertId: null,
              expertName: null,
              type:null,
              userId: null,
              clinicsNo: null,
              pbStartTime :{ ymd: "", hms: "" },
              pbEndTime :{ ymd: "", hms: "" },
              sex:"————",
              status:res.status
            };
              this.epxertsChart = expertChart;
              this.toAndfrm=null;
              this.expertChertByOdering();
              this.masageList=[];
              return;
          }else {
            expertChart.status=res.status;
            this.epxertsChart = expertChart;
          }

        }
      }
    },
    //发送消息
    text: function() {
      var str = document.getElementById('testInput').value;
      str = str.replace(/(^\s*)|(\s*$)/g, "");
      if (!this.toAndfrm) {
        this.warning("没有订单，会话不存在！");
        return false
      };
      if (this.toking != this.toAndfrm || this.status == 1) {
        this.warning("该订单没有开始协诊！");
        return false
      }
      if (!str) {
        this.warning("消息不能为空");
        return false;
      }
      var that = this;
      var time = publicFn.publicLo.dataChange();
      time = time.ymdTiem;
      var texts =
        {
          "id": "", "type": "chat", from: "", "to": that.forName, "data": this.da, "ext": { orderId: that.toAndfrm }, "error": false, "errorText": "",
          "errorCode": "",
          "timeStamp": 1499072478613,
          "left": true,
          status: "text",
          toAndfrm: that.toAndfrm,
          time: time
        };
      //          that.$store.commit('pushMasge',texts);
      var id = conn.getUniqueId();                 // 生成本地消息id
      var msg = new WebIM.message('txt', id);      // 创建文本消息
      var yAndN = false;
      msg.set({
        msg: texts.data,                  // 消息内容
        to: that.forName,
        ext: { orderId: that.toAndfrm }, // 接收消息对象（用户id）
        roomType: false,
        success: function(id, serverMsgId) {
          //发送成功后保存聊天消息
          that.savMasge(texts);
          //发送成功后 渲染消息
          that.$store.commit('pushMasge', texts);
          // hat.$store.commit('pushTookenM',that.toAndfrm);
          that.da = "";
        }
      });
      msg.body.chatType = 'singleChat';
      conn.send(msg.body);
    },
    //发送图片消息
    imgSen: function() {
      if (!this.toAndfrm) {
        this.warning("没有订单，会话不存在！");
        return false
      };
      if (this.toking != this.toAndfrm || this.status == 1) {
        this.warning("该订单没有开始协诊！");
        return false
      }
      var that = this;
      var id = conn.getUniqueId();
      var msg = new WebIM.message('img', id);
      var input = document.getElementById('filet');               // 选择图片的input
      var file = WebIM.utils.getFileUrl(input);
      var allowType = {
        'jpg': true,
        'gif': true,
        'png': true,
        'bmp': true
      };
      if (file.filetype.toLowerCase() in allowType) {
        var option = {
          apiUrl: WebIM.config.apiURL,
          file: file,
          to: that.forName,
          roomType: false,
          ext: { orderId: that.toAndfrm },
          chatType: 'singleChat',
          onFileUploadError: function() {
          },
          onFileUploadComplete: function() {
          },
          success: function(e) {
            var time = publicFn.publicLo.dataChange();
            time = time.ymdTiem;
            var imgs = {
              "id": id,
              "type": "chat",
              "from": "13212345678",
              "to": that.forName,
              "url": msg.body.body.url,
              ext: { orderId: that.toAndfrm },
              status: "img",
              "left": true,
              toAndfrm: that.toAndfrm,
              time: time
            };
            that.$store.commit('pushMasge', imgs);
            that.savMasge(imgs);
          },
          // flashUpload: WebIM.flashUpload               // 意义待查
        };
        msg.set(option);
        var x = conn.send(msg.body);
      }
