      var str = this.user.name+"发起了一次通话";
      var texts =
        {
          "id": "", "type": "chat", from: "", "to": this.forName, "data": str, "ext": { orderId: this.toAndfrm, orderContact: this.user.name }, "error": false, "errorText": "",
