          "timeStamp": 1499072478613,
          "left": true,
          status: "text",
          toAndfrm: this.toAndfrm,
        };
      var id = conn.getUniqueId();                 // 生成本地消息id
      var msg = new WebIM.message('txt', id);      // 创建文本消息
      msg.set({
        msg: texts.data,                  // 消息内容
        to: this.forName,
        ext: { orderId: this.toAndfrm, orderContact: this.user.name }, // 接收消息对象（用户id）
        roomType: false,
