<template>
  <div class="">
    <div>
      <CommonHeader></CommonHeader>
    </div>
    <div class="fix">
      <div class="dataDictionary">
        <Set></Set>
      </div>
    </div>
    <!--主要内容-->
    <div class="dic-content">
      <div class="center clearfix">
        <div class="clearfix slectList">
          <el-tree class="tree-left" :data="dataList" :props="defaultProps"  node-key="id" :default-expanded-keys="sleId" accordion @node-click="handleNodeClick">
          </el-tree>
        </div>
        <div class="cent">
          <!--<div class="" style="width: 100% ;margin-right: 30px;">-->
          <div class='P-top'>
            <i class='leftBorder'></i>
            <span>{{fromDat.parentNam}}设置</span>
            <button type="button" class="btn btn-default  pull-right requestBtn" v-on:click="saveWay('addChaild')"><i class="fa fa-plus-square-o"></i> 新增字典子项</button>
            <button type="button" class="btn btn-danger  pull-right requestBtn" v-on:click="saveWay('del')"><i class="fa fa-trash-o"></i> 删除数据字典</button>
            <button type="button" class="btn btn-default  pull-right requestBtn"  v-on:click="saveWay('addThis')"><i class="fa fa-plus"></i> 新增数据字典</button>
          </div>
          <!--</div>-->
          <div class="textCent">
            <ul class="nav nav-tabs" v-show="addChaild" style="margin-top: 20px;">
              <li role="presentation" v-for="list in muent"  :class="{'active':list.cheak}" v-on:click="muentClick(list)"><a href="javascript:;">{{list.text}}</a></li>
            </ul>
            <div class="card" style="margin-top: 30px;">
              <div class=''v-show="centId==1" >
                <div class='P-content-bottom'>
                  <table class="table table-bordered">
                    <thead>
                    <tr>
                      <th>字典名称</th>
                      <th>字典值</th>
                      <th>创建人</th>
                      <th>创建时间</th>
                      <th>是否启用</th>
                      <th>操作</th>
                    </tr>
                    </thead>
                    <tbody>
                    <tr  v-for="list in tableList">
                      <td>{{list.label}}</td>
                      <td>{{list.value}}</td>
                      <td>{{list.chineseName}}</td>
                      <td>{{list.createdWhen}}</td>
                      <td>
                        <span v-show="list.status == 1">已启用</span>
                        <span v-show="list.status == 0">未启用</span>
                      </td>
                      <td>
                        <button type="button" class="btn btn-primary btn-sm details-btn" @click="gotSav(list)"><i class="fa fa-edit"></i> 编辑</button>
                        <button type="button" class="btn btn-danger btn-sm details-btn" @click="deleteDictory(list)"><i class="fa fa-trash"></i> 删除</button>
                      </td>
                    </tr>
                    <tr v-if="tableList == null || tableList.length == 0">
                      <td colspan="6">
                        <span>暂无数据</span>
                      </td>
                    </tr>
                    </tbody>
                  </table>
                </div>
              </div >
              <el-form :model="fromDat" :rules="rules" ref="fromDat" label-width="150px" class="demo-ruleForm clearfix"  v-show="centId==2">
                <el-form-item label="父级字典：" prop="parentName">
                  <el-select v-model="fromDat.parentId" placeholder="请选择" class="noe">
                    <el-option v-for="item in options1" :key="item.value" :label="item.label" :value="item.value">
                    </el-option>
                  </el-select>
                  <p >{{fromDat.parentNam}} </p>
                </el-form-item>
                <el-form-item label="字典名称：" prop="label">
                  <el-input  class="noe" v-model="fromDat.label"></el-input>
                  <p >{{fromDat.label}} </p>
                </el-form-item>
                <el-form-item label="字典值：" prop="value">
                  <el-input  class="noe" v-model="fromDat.value"></el-input>
                  <p >{{fromDat.value}} </p>
                </el-form-item>
                <el-form-item label="是否启用：">
                  <el-radio-group v-model="fromDat.status">
                    <el-radio :label="1" class="noe" style="width: 100px">启用</el-radio>
                    <el-radio :label="0" class="noe" style="width: 100px">未启用</el-radio>
                  </el-radio-group>
                  <i class="noe1" v-show="fromDat.status==1" style="width: 100px;color: #000;font-style: normal;">启用 </i>
                  <i class="noe1"  v-show="fromDat.status==0" style="width: 100px;color: #000; font-style: normal;" >未启用 </i>
                </el-form-item>
                <el-form-item label="排序id：" prop="sortId">
                  <el-input  class="noe" v-model="fromDat.sortId"></el-input>
                  <p >{{fromDat.sortId}} </p>
                </el-form-item>
                <el-form-item label="菜单描述：" prop="description">
                  <el-input  class="noe" v-model="fromDat.description"></el-input>
                  <p >{{fromDat.description}}</p>
                </el-form-item>
                <el-form-item>
                  <button type="button" class="btn btn-default  pull-right requestBtn" v-on:click="saveFn('del')" ><i class="fa fa-times"></i> 取消</button>
                  <button type="button" class="btn btn-primary  pull-right requestBtn" v-on:click="saveFn(save)"><i class="fa fa-save"></i> 保存</button>
                </el-form-item>
              </el-form>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<style scoped="">
  .dic-content{
    margin:50px 15px 0px 195px;
    min-width:960px;
  }
  .P-top {
    padding:5px 0px;
    font-size:16px;
    height:50px;
    background:white;
    position:relative;
    border-bottom:1px solid #ddd;
  }

  .leftBorder {
    display:block;
    width:1px;
    height:16px;
    background:#6886d4;
    position:absolute;
    left:0px;
    top:16px;
  }

  .center{ }

  .slectList {
    min-width:200px;
    float: left;
    padding-bottom: 80px;
    overflow: auto;
    background: #EAEDF1;
  }
  .tree-left {
    font-size: 16px;
    background: #EAEDF1;
  }

  .textCent{margin-top: 20px;}
  .cent{margin-left:200px;}
  .requestBtn{margin-left: 10px;}
  .nav{height: 40px;}
  tr td {height: 40px;line-height: 40px;}
  /*上部分样式*/
  .P-content .P-content-top{
    height:20px;
  }
  /*下部分表格样式*/
  .P-content-bottom table th,.P-content-bottom table{
    text-align:center;
  }
  .P-content-bottom .table thead{
    background:#f5f5f5;
  }
  /*table样式*/
  .P-content-bottom .table tr{
    text-align:center;
  }
  .P-content-bottom .table td{
    padding:4px 0px;
    line-height:39px;
  }
  .P-content-bottom .table th{
    text-align:center;
    border-bottom:none;
  }
  .el-form-item{height: 43px}
  .noe p{display: none}
  .noe{width:400px;}
  .block .noe{display: none}
  .block p{display: inline}
  .block .noe1{display: inline}
  .noe1{display: none}
  form p{ display: none}
  form{width:550px;}
  .slectList{position: fixed;top:50px;left: 179px;}
  .el-tree{border: none}

</style>
<script>
  //header
  import CommonHeader from '../public/header.vue';
  //menu
  import Set from '../public/set.vue';
  //进度条
  import NavTop1 from '../public/nva1.vue';
  //患者列表
  import PatientList from '../patient/component/patientList.vue';
  //聊天窗口 toking
  import Toking from '../assistOrderIng/component/toking.vue';
  import publicFn from '../../untils/Public.js';
  //基本资料
  //import BasicData from '../experts/basicData.vue';
  //就诊历史
  //import HistoricalRecord from '../experts/historicalRecord.vue';
  // import bus from '../../../src/common/common.js'
  export default {
    name: "dataDictionary",
    created: function () {

    },
    data:function() {
      return{
        //
        token:localStorage.getItem('access_token'),
        //
        parentId:null,
        selectedId:'',//左边栏选中的id
        //添加方式
        addChaild:true,
        //保存方式
        save:"add",
        sleId:null,
        //展示主体
        centId:1,
        //校验
        rules: {
          label: [
            {required: true, message: '请输入字典名称', trigger: 'blur'},
            {min: 0, max: 25, message: '长度最多为25个字符', trigger: 'blur'}
          ],
          sortId: [
            {required: true, message: '请输入排序Id', trigger: 'blur'},
            { validator: (rule, value, callback) => { if (/^-?(?:\d+|\d{1,3}(?:,\d{3})+)?(?:\.\d*)?$/.test(value) == false) {
              callback(new Error("请输入数字")); } else { callback(); } }, trigger: 'blur' }
          ],
          value: [
            {required: true, message: '请输入字典值', trigger: 'blur'},
            {min: 0, max: 100, message: '长度最多为100个字符', trigger: 'blur'}
          ],
          description:[
            {min: 0, max: 200, message: '长度最多为200个字符', trigger: 'blur'}
          ]
        },
        //下拉菜单
        dataList:[{
          id:-1,
          label: '胡大一管理系统',
          children: [],
          "sortId":null,
          "parentId":null,
          "value":null,
          "createdBy":null,
          "createdWhen":{times:null},
          "updatedBy":null,
          "updatedWhen":{times:null},
          "status":null,
          "description":null
        }],
        defaultProps: {
          children: 'children',
          label: 'label'
        },
        //muent
        muent:[
          {text:"字典子项列表",id:1,cheak:true},
          {text:"字典子项基本信息",id:2,cheak:false}
        ],
        tableList:[],
        options:[],
        options1:[],
        dictionaryList:[],
        chaid:[],
        fromDat:{
        }
      }
    },
    methods:{
      //
      handleNodeClick(list) {
        this.muent[0].cheak=true;
        this.muent[1].cheak=false;
        this.centId=1;
        this.selectedId = list.id;
        if(-1 != list.id) {
          //点击父级字典时不加载
          this.dictionarylist(list.id,list.label);
        }
      },
      muentClick(list){
        for(let i in this.muent){
          this.muent[i].cheak=false
        }
        list.cheak=true;
        this.centId=list.id
        if(list.id==2){
          if(this.fromDat.parentId==-1){
            this.options1=[{
              value: -1,
              label:"胡大一管理系统"
            }];
          }else {
            this.options1=this.options;
          }
        }
      },
      //
      saveWay(a){
        /*新增字典*/
        if(a=="addThis"){
          this.save="addThis";
          this.fromDat.id=null;
          this.fromDat.label=null;
          this.fromDat.sortId=null;
          this.fromDat.value=null;
          this.fromDat.status=1;//默认启用
          this.fromDat.description=null;
          this.fromDat.parentId=-1;
          this.fromDat.parentNam="胡大一管理系统";
          this.addChaild=false;
          this.centId=2;
          this.options1=[{
            value: -1,
            label:"胡大一管理系统"
          }];

          $('.el-tree-node .is-current').attr('style', 'color:black;background:#EAEDF1;');//清除选中样式
          this.selectedId = '';//设置左边选中为空
        } else if(a=="addChaild"){
          /*新增字典子项*/
          if(this.selectedId == null || this.selectedId == '') {
            this.warning('请先选择左边的字典');
            return;
          }
          this.save="addChaild";
          this.fromDat.id=null;
          this.fromDat.label=null;
          this.fromDat.sortId=null;
          this.fromDat.value=null;
          this.fromDat.status=1;//默认启用
          this.fromDat.description=null;
          this.fromDat.parentId=this.selectedId;
          this.addChaild=false;
          this.centId=2;
          if( this.fromDat.parentId==-1){
            this.options1=[{
              value: -1,
              label:"胡大一管理系统"
            }];
          }else {
            this.options1=this.options
          }
        } else if(a == 'del') {
          /*删除字典*/
          if(this.selectedId == null || this.selectedId == '') {
            this.warning('请先选择左边的字典');
            return;
          }

          this.$confirm('确定删除该字典', '提示', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning'
          }).then(() => {
            //确定
            var url = '/api/basic/dictionary/' + this.selectedId;
            this.$http.delete(url, {
              headers: {
              }
            }).then(function () {
              this.success('删除成功');
              this.dictionary(-1,this.dataList[0]);//加载列表数据
            }, function (res) {
              this.error('删除失败');
            });
          }).catch(() => {
            return;
          });
        }
      },
      //
      saveFn(a){
//        if(a=='addThis'){
//          this.postSave();//保存数据字典
//        }
        if(a=='addThis' || a=="addChaild"){
          this.postAdd();//保存数据字典
        }
        if(a=="del"){
          this.dictionary(-1,this.dataList[0]);
          this.addChaild=true;
          this.centId=1;
        }
      },
      //新增数据字典保存
      postAdd(){
        debugger;
        var parm ={
          "name":this.fromDat.label,
          "sortId":parseInt(this.fromDat.sortId),
          "parentId":this.fromDat.parentId,
          "value":this.fromDat.value,
          "status":this.fromDat.status,
          "description":this.fromDat.description,
        };
        parm=JSON.stringify(parm);
        var url="/api/basic/dictionary";
        this.$refs['fromDat'].validate((valid) => {
          if (valid) {
            this.$http.post(url,parm,{
              headers:{
                Authorization: 'bearer '+this.token
              },
              emulateJSON:true
            }).then(function(res){
              res=res.body;
              if(res == 1){
                this.success("保存成功！");
                if(this.selectedId != '') {
                  this.dictionarylist(this.selectedId,null);
                } else {
                  this.dictionary(-1,this.dataList[0]);
                  this.tableList = [];
                }
                this.addChaild=true;
                this.centId=1;
              }
            },function (res) {
              this.error('保存失败');
            });

          } else {
            return;
          }
        });
      },
      //编辑保存
      postSave(){
//       var parm ={
//         "id":this.fromDat.id,
//         "name":this.fromDat.label,
//         "sortId":this.fromDat.sortId,
//         "parentId":this.fromDat.parentId,
//         "value":this.fromDat.value,
//         "status":this.fromDat.status,
//         "description":this.fromDat.description,
//         };
//       parm=JSON.stringify(parm);
//      var url="api/basic/dictionary";
//        this.$http.put(url,parm,{
//          headers:{
//            Authorization: 'bearer '+this.token
//          },
//          emulateJSON:true
//        }).then(function(res){
//            res=res.body;
//            if(res==1){
//              this.success("保存成功！");
//              this.addChaild=true;
//              this.centId=1;
//            }
//        }).catch(function (res) {
//          this.warning('增加失败');
//        });
      },
      //左边字典列表
      dictionary(parm,list){
        var url="/api/basic/dictionary/parent?parentId="+parm;
        this.$http.get(url,{
          headers:{
          }
        }).then(function(res){
          res=res.body;
          var pus=[];
          var optio=[];
          for(let i in res){
            var time=publicFn.publicLo.timeCheng(res[i].createdWhen);
            var time2=publicFn.publicLo.timeCheng(res[i].createdWhen);
            var pu={
              "id":res[i].id,
              "label": res[i].name,
              "children": [],
              "sortId":res[i].sortId,
              "parentId":res[i].parentId,
              "value":res[i].value,
              "createdBy":res[i].createdBy,
              "createdWhen":time.times,
              "updatedBy":res[i].updatedBy,
              "updatedWhen":time2.times,
              "status":res[i].status,
              "description":res[i].description,
              parentNam:list.label,
              chineseName:res[i].chineseName
            };
            var op={
              value:res[i].id,
              label:res[i].name
            };
            pus.push(pu);
            optio.push(op);
          }
          list.children=pus;
          this.sleId=[0,list.id];
          this.options=optio
        }).catch(function (res) {
          this.warning(res.masage)
        });
      },
      //右边字典值列表
      dictionarylist(parm,name){
        var parentUrl = 'api/basic/dictionary/'+parm;
        this.$http.get(parentUrl, {
          headers: {
          }
        }).then(function (res) {
          //赋值
          this.fromDat = res.body;
          this.fromDat.parentNam = name;
          this.fromDat.label = res.body.name;

          //修改选中样式
          if($('.el-tree-node').hasClass('is-current')) {
            $('.el-tree-node .is-current').attr('style', 'color:#20a0ff;background:white;');
            $('.el-tree-node .is-current').siblings().attr('style', 'color:black;background:#EAEDF1;');
          }
        },function (err) {
          this.error("数据加载异常");
        });


        var url="/api/basic/dictionary/parent?parentId="+parm;
        this.$http.get(url,{
          headers:{
          }
        }).then(function(res){
          res=res.body;
          var optio=[];
          var chadin=[];
          if(!res[0]){
            chadin={
              "children": [],
              "parentId":parm,
              parentNam:name
            };
            this.fromDat=chadin;
            if(res == '' && res.length < 1) {
              this.tableList = [];
              return;
            }
          }else {
            for(let i in res){
              var time=publicFn.publicLo.timeCheng(res[i].createdWhen);
              var time2=publicFn.publicLo.timeCheng(res[i].createdWhen);
              var pu={
                "id":res[i].id,
                "label": res[i].name,
                "children": [],
                "sortId":res[i].sortId,
                "parentId":res[i].parentId,
                "value":res[i].value,
                "createdBy":res[i].createdBy,
                "createdWhen":time.times,
                "updatedBy":res[i].updatedBy,
                "updatedWhen":time2.times,
                "status":res[i].status,
                "description":res[i].description,
                parentNam:name,
                chineseName:res[i].chineseName
              };
              chadin.push(pu);
            }
//            this.fromDat=chadin[0];
            this.tableList=chadin;
          }
        }).catch(function (res) {
          this.warning(res.masage)
        });
      },
      //编辑
      gotSav(list){
//        this.fromDat=list;
//        if( this.fromDat.parentId==-1){
//          this.options1=[{
//            value: -1,
//            label:"胡大一管理系统"
//          }];
//        }else {
//          this.options1=this.options
//        }
//        this.centId=2;
//        this.save="add";
//        this.muent[0].cheak=false;
//        this.muent[1].cheak=true;
        this.$router.push({ name: 'DictionaryDetail', query: { id: list.id } });
      },
      // 判断屏幕的大小，获取患者第二级导航的高度
      getClientHeight() {
        $('.slectList').css({
          height: document.documentElement.clientHeight - 30 + 'px'
        });
        $(window).resize(function () {
          $('.slectList').css({
            height: document.documentElement.clientHeight - 30 + 'px'
          })
        })
      },
      // 删除数据字典
      deleteDictory(element) {
        this.$confirm('确定删除该字典', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          //确定
          var url = '/api/basic/dictionary/' + element.id;
          this.$http.delete(url, {
            headers: {
            }
          }).then(function () {
            this.success('删除成功');
            this.dictionarylist(element.parentId,element.parentNam);//加载列表数据
          }, function (res) {
            this.error('删除失败');
          });
        }).catch(() => {
          return;
        });
      },
      open:function() {
        this.$message('这是一条消息提示');
      },
      success:function(text) {
        this.$message({
          message: text,
          type: 'success'
        });
      },
      warning:function(text){
        this.$message({
          message: text,
          type: 'warning'
        });
      },
      // 失败提示框
      error() {
        this.$message({
          message: '保存失败',
          type: 'error'
        });
      },
    },

    mounted(){
      this.getClientHeight();
//      this.dictionarylist(-1,"胡大一管理系统");
      this.dictionary(-1,this.dataList[0]);
    },
    components: {
      CommonHeader: CommonHeader,
      Set:Set,
      NavTop1:NavTop1,
      PatientList:PatientList,
      toking:Toking
    }
  }
</script>



