<template>
    <div class="foldBox">
      <div  v-bind:docterId="user">
        <div class="panel-group" v-bind:tokingM="tokingM" id="accordion" role="tablist" aria-multiselectable="true" >
          <div class="panel panel-default" :OderingData="OderingData">
            <div class="panel-heading foldTop" role="tab" id="headingOne">
              <h4 class="panel-title" :patientData="patientData">
                <i  :class="{'glyphicon glyphicon-chevron-down':iconOff01,'glyphicon glyphicon-chevron-up':!iconOff01}" style="color: #fff; font-size: 12px;" ></i>
                <a role="button" class="" data-toggle="collapse" data-parent="#accordion" href="#collapseOne" aria-expanded="true" aria-controls="collapseOne" v-on:click="iconOff(1)">
                  今日协诊（{{pationListDateSize}}）
                </a>
              </h4>
            </div>
            <div id="collapseOne" class="panel-collapse collapse in" role="tabpanel" aria-labelledby="headingOne">
              <div class="panel-body foldCent">
                <ul>
                  <li class=""v-for="(lis,index) in pationListDate" :status="lis.check" v-on:click="clickOder(lis)">
                    <div class="listCentBox fix">
                      <div class="text col-sm-5">
                        <p class="name">{{lis.patientName}}</p>
                        <p v-show="lis.status==1" style="width: 150px;">预约 {{lis.startTime.hms}}-&nbsp{{lis.endTime.hms}}</p>
                        <p v-show="lis.status==2">{{lis.startTime.hms}}</p>
                      </div>
                      <div class="text col-sm-5">
                        <p>{{lis.recordValue}}  </p>
                        <!--<p v-show="type==1">{{lis.expertName}}</p>-->
                        <!--<p v-show="type==2">{{lis.doctorName}}</p>-->
                        <p v-show="lis.status==2"> 协诊中 </p>
                      </div>
                      <div class="stats">
                        <p>{{lis.typeText}}</p>
                      </div>
                      <i class="rede" v-show="redList[index].red"></i>
                      <i class="ceLeft" v-show="lis.timeOk">
                        到预约时间
                      </i>
                      <div class="statsBorder">
                      </div>
                    </div>
                  </li>
                </ul>
              </div>
            </div>
          </div>
          <div class="panel panel-default" style="margin-top: 20px">
            <div class="panel-heading foldTop" role="tab" id="headingTwo">
              <h4 class="panel-title">
                <i :class="{'glyphicon glyphicon-chevron-down':iconOff02,'glyphicon glyphicon-chevron-up':!iconOff02}" style="color: #fff; font-size: 12px;" ></i>
                <a class="collapsed " role="button" data-toggle="collapse" data-parent="#accordion" href="#collapseTwo" aria-expanded="false" aria-controls="collapseTwo" v-on:click="iconOff(2)">
                  后续协诊（{{pationListDateLastSize}}）
                </a>
              </h4>
            </div>
            <div id="collapseTwo" class="panel-collapse collapse" role="tabpanel" aria-labelledby="headingTwo">
              <div class="panel-body foldCent">
                <ul>
                  <li class="" status="1" v-for="lis in pationListDateLast" :status="lis.check" v-on:click="clickOder(lis)">
                    <div class="listCentBox fix">
                      <div class="text col-sm-5">
                        <p class="name">{{lis.patientName}}</p>
                        <p style="width: 150px;">预约&nbsp{{lis.startTime.ymd}}</p>
                      </div>
                      <div class="text col-sm-5">

                        <p >{{lis.recordValue}}</p>
                        <!--<p v-show="type==1">{{lis.expertName}}</p>-->
                        <!--<p v-show="type==2">{{lis.doctorName}}</p>-->
                        <p v-show="lis.status==2"> 协诊中 </p>
                        <p style="min-width:80px;">{{lis.startTime.hms}}-&nbsp{{lis.endTime.hms}}</p>
                      </div>
                      <div class="stats">
                        <p>{{lis.typeText}}</p>
                      </div>
                      <div class="statsBorder">
                      </div>
                    </div>
                  </li>
                </ul>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
</template>
<style scoped>
  li{overflow: hidden}
  .foldBox .foldCent{border: none ;padding: 0px;max-height: 700px;overflow-y: scroll;padding-right: 5px;}
  .panel-body{padding: 0px;}
  .panel{border: none;margin-top: 0px;}
  .rede{border-radius:10px; display: block;width: 10px;height: 10px; position: absolute;right: -2px;top: -2px;z-index:99;background-color: red}
  .ceLeft{color: #fff;text-align: center; background-color: #f40; display: block;width:100px;height:30px;line-height: 30px; position: absolute;right: -24px;top: 10px;-moz-transform:rotate(45deg);  -webkit-transform:rotate(45deg);}
  .name{height: 30px;width: 100%;overflow: hidden}
</style>
<script>
  import publicFn from '../../../untils/Public.js';
  import {mapActions,mapGetters,mapState} from 'vuex'
    export default {
        name:"patientList",
      data:function(){
        return{
          //
          iconOff01:true,
          iconOff02:true,
          // 预约时间
          timeOk:null,
          //专家医生管理员端口
          type:1,
          //今日协诊
          pationListDate:[],
          //后续协诊
          pationListDateLast:[],
          //后续协诊总数
          pationListDateLastSize:0,
          //今日协诊总数
          pationListDateSize:0,
          //患者id
          pationId:0,
          //订单 专家 parm
           parm:null,
          //医生id
          doctorId:null,
          //
          yn:true,
          //this.tokingMlist
          tokingMlist:[],
          //红点
          redList:[{id:0,red:false}],
          //上次患者列表
          dataListOrld:null
        }
      },
      created: function () {
        if(this.aler){
          this.parm=this.aler;
        }else {
          this.parm=this.$route.query;
        }
        if(this.parm.id){
          //是否从订单过来 从订单过来直接
          this.$store.commit('OderingPostitle',this.parm);
          this.tookinglist(this.parm.id);
        };
      },
      mounted:function() {

      },
      methods: {
        ...mapActions({
          ApationeList:'ApationeList',
          postOderChart:"postOderChart",
          tookinglist:"tookinglist"
        }),
        //
        iconOff(icon){
          if(icon==1){
            this.iconOff02=false;
               if(this.iconOff01){
                 this.iconOff01=false;
                 return
               }
               if(!this.iconOff01){
                 this.iconOff01=true;
                 return
               }
          }
          if(icon==2){
            this.iconOff01=false;
            if(!this.iconOff02){
              this.iconOff02=true;
              return
            }else {
              this.iconOff02=false;
              return
            }
          }
        },
        //患者列表
        pantioneData(res){
         //"患者列表被刷新";
          var redListPu=[];
          //今日协诊患者初始列表（无选中状态）
          var listIngToday=res.listIng.listin;
          //后续协诊患者初始列表（无选中状态）
          var listWalToday=res.listIng.listWal;
          var listIng=this.pantionFn( listIngToday.concat(listWalToday));
          var listWall=this.pantionFn(res.listWall);
          var parm={id:null,name:null, orderNo:null, paitornt:null, doctorId:null};
          //从点的过来的列表默认过来的列表
          if(this.aler){
            this.parm=this.aler;//记录上次订单，刷新列表不会失去焦点
          }
          if(this.parm){//记录上次订单，刷新列表不会失去焦点
            for(let i in listIng.data){
              listIng.data[i].check = 0;
               if(listIng.data[i].id==this.parm.orderNo){
                 //check、选中状态 0：为选中， 1：选中
                 listIng.data[i].check=1;
                 parm={
                   id:listIng.data[i].expertId,
                   name:listIng.data[i].patientName,
                   orderNo:listIng.data[i].id,
                   paitornt:listIng.data[i].patientId,
                   doctorId:listIng.data[i].doctorId,
                 }
                 if(listIng.data[i].timeOk){
//                   parm.timeOk=listIng.data[i].timeOk
                 }
                 this.tookinglist(parm.orderNo);
                 //刷新右边
                 this.postOderChart(parm.orderNo);
               }
            }
          }
          //无上次订单，默认今日协诊第一单
          if( !this.parm.id && listIng && listIng.data[0]){
            listIng.data[0].check=1;
            parm={
              id:listIng.data[0].expertId,
              name:listIng.data[0].patientName,
              orderNo:listIng.data[0].id,
              paitornt:listIng.data[0].patientId,
              doctorId:listIng.data[0].doctorId,
            }
            if(listIng.data[0].timeOk){
//              parm.timeOk=listIng.data[0].timeOk
            }
            if(listIng.data[0].status==2){
              this.tookinglist(parm.orderNo);
            }
            //刷新右边
            this.postOderChart(parm.orderNo);
          }
          //没有从订单过来，也没有今日协诊，默认后续协诊第一单
          if(!listIng.data[0] && !this.parm.id && listWall && listWall.data[0]){
            listWall.data[0].check=1;
            parm={
              id:listWall.data[0].expertId,
              name:listWall.data[0].patientName,
              orderNo:listWall.data[0].id,
              paitornt:listWall.data[0].patientId,
              doctorId:listWall.data[0].doctorId,
            }
            if(listWall.data[0].timeOk){
//              parm.timeOk=listWall.data[0].timeOk
            }
            //刷新右边
            this.postOderChart(parm.orderNo);
          }
          //今日协诊患者列表
          if(listIng){
            this.pationListDate=listIng.data;
            if(listIng.cunt){
              this.pationListDateSize=listIng.cunt;
            }
          }else {
            this.pationListDateSize=0;
          }
          //后续协诊患者列表
          if(listWall){
            this.pationListDateLast=listWall.data;
            if(listWall.cunt){
              this.pationListDateLastSize=listWall.cunt
            }
          }else {
            this.pationListDateLastSize=0
          }
          for (let i in listIng.data){
            redListPu.push({red:false,id:listIng.data[i].id})
          }
          this.redList=redListPu;
          this.$store.commit('OderingPostitle',parm);
          //更新订单详情（vuex ）
          if( this.pationListDateSize>0){
            this.postOderChart(parm.orderNo);
          } else {
            this.$store.commit('pushOderChart',-100);
          }
          //清除已读消息（vuex）
          this.$store.commit('sliceTookenM',parm.orderNo);
        },
        //患者列表数据解析
        pantionFn(res){
          //初始订单列表数据
          var data=[];
          if(!res){
            // 不存在返回null
            return null
          }
          var cunt //列表数字
          for(let i in res){
            //当前时间对象
            var time = publicFn.publicLo.dataChange(res[i].now);
            //开始时间 时间戳
            var time2= res[i].startTime;
            //typeText：问诊描述，endTime：预约结束时间 ，startTime：预约开始时间
            var typeText,endTime,startTime;
            if(res[i].type==0){typeText="图文"}
            if(res[i].type==1){typeText="视频"}
            endTime=publicFn.publicLo.timeCheng(res[i].endTime);
            startTime=publicFn.publicLo.timeCheng(res[i].startTime);
            var recordTypeText;
            switch (res[i].recordType){
              case 1:
                recordTypeText="高血压";
                break;
              case 2:
                recordTypeText="心电生理";
                break;
              case 3:
                recordTypeText="冠脉血管" ;
                break;
              case 4:
                recordTypeText="心力衰竭";
                break;
              default :
                recordTypeText="---";
                break
            }
            var check = 0;//默认未选中
            var date={
              patientName:res[i].patientName,// "康医生",
              orderNo:res[i].orderNo,//  "AO1707130001",
              expertId:res[i].expertId,//  2,
              expertName: res[i].expertName,// "康专家",
              doctorTitle:res[i].doctorTitle,//  "主治医生",
              totalPrice:res[i].totalPrice,//  3.5,
              recordType:res[i].recordType,//  1,
              recordTypeText:recordTypeText,
              description: res[i].description,// "testteststest",
              type:res[i].type,//  0,
              recordId:res[i].recordId,//  1,
              doctorName:res[i].doctorName,//  "康医生",
              doctorId:res[i].doctorId,//  1,
              hospitalId:res[i].hospitalId,//  1,
              expertTitle:res[i].expertTitle,//  "医学专家",
              paidIn:res[i].paidIn,//  3.5,
              usersId:res[i].usersId,//  1,
              startTime:startTime,//  ,
              id:res[i].id,// 1,
              endTime: endTime,//1500082927000,
              scheduleId:res[i].scheduleId,// 1,
              skuId:res[i].skuId,// 1,
              status:res[i].status,// 1
              typeText:typeText,
              check:check,
              recordValue:res[i].recordValue,
              patientId:res[i].patientId
            };
            if(date.patientName.length>=5){
              date.patientName= date.patientName.substring(0,5)+"..."
            }
            //专家 预约时间以到，开始协诊和标签
            if(this.user.userType==2 && time.time>time2 && res[i].status==1){
              date.timeOk = true
            }
            //管理员过滤非自己的订单
            if(this.user.roleId =="1313" && this.user.id!=date.expertId){

            }else if(this.user.roleId == "1312" && this.user.id!=date.doctorId){

            }else {
              data.push(date);
            }
          }
          //data ：订单列表，cunt：订单总数
          return {data:data,cunt:data.length}
        },
        //选择订单
        clickOder: function(data){
          // expertId：专家id，id：订单id，patientId，患者id，name：患者名称，doctorId：医生id，status：订单状态
         var expertId=data.expertId;
         var id=data.id;
         var patientId=data.patientId;
         var name=data.patientName;
         var doctorId=data.doctorId;
         var status=data.status;
         //初始化选中状态，并且 从新标记选中状态(今日协诊列表)
          for(let i in this.pationListDateLast){
            this.pationListDateLast[i].check=0;
             if(this.pationListDateLast[i].id==id){
               this.pationListDateLast[i].check=1;
             }
          }
         //初始化选中状态，并且 从新标记选中状态（后续协诊列表）
          for(let i in this.pationListDate){
             this.pationListDate[i].check=0
             if(this.pationListDate[i].id==id){
               this.pationListDate[i].check=1;
             }
         }

         var parm={
           id:expertId,
           name:name,
           orderNo:id,
           paitornt:patientId,
           doctorId:doctorId,
         }
         if(data.timeOk){
            parm.timeOk=data.timeOk
         }
         this.postOderChart(parm.orderNo);
         //记录上次聊天订单
         this.$store.commit('OderingPostitle',parm);
         //订单为协诊中是获取聊天记录
         if( status == 2 ){
           this.tookinglist(parm.orderNo);
         }
         //清除未读消息状态（vuex）
         this.$store.commit('sliceTookenM',id);

       },
        //未读消息
        notRading(parm){
         for(let i in  parm){
           //循环 今日协诊列表，标记未读消息
            for (let k in this.redList){
              //匹配对应订单列表，标记未读消息
              if( parm[i].id==this.redList[k].id && this.redList[k].id != this.aler.orderNo){
                this.redList[k].red=true;
              }
            }
         }
        },
        //数据库字典
        //判断两个对象是否相等
        equal(objA, objB) {
          if (typeof arguments[0] != typeof arguments[1])
            return false;
          //数组
          if (arguments[0] instanceof Array)
          {
            if (arguments[0].length != arguments[1].length)
              return false;
            var allElementsEqual = true;
            for (var i = 0; i < arguments[0].length; ++i)
            {
              if (typeof arguments[0][i] != typeof arguments[1][i])
                return false;
              if (typeof arguments[0][i] == 'number' && typeof arguments[1][i] == 'number')
                allElementsEqual = (arguments[0][i] == arguments[1][i]);
              else
                allElementsEqual = arguments.callee(arguments[0][i], arguments[1][i]);            //递归判断对象是否相等
            }
            return allElementsEqual;
          }
          //对象
          if (arguments[0] instanceof Object && arguments[1] instanceof Object)
          {
            var result = true;
            var attributeLengthA = 0, attributeLengthB = 0;
            for (var o in arguments[0])
            {
              //判断两个对象的同名属性是否相同（数字或字符串）
              if (typeof arguments[0][o] == 'number' || typeof arguments[0][o] == 'string')
                result = eval("arguments[0]['" + o + "'] == arguments[1]['" + o + "']");
              else {
                //如果对象的属性也是对象，则递归判断两个对象的同名属性
                //if (!arguments.callee(arguments[0][o], arguments[1][o]))
                if (!arguments.callee(eval("arguments[0]['" + o + "']"), eval("arguments[1]['" + o + "']")))
                {
                  result = false;
                  return result;
                }
              }
              ++attributeLengthA;
            }
            for (var o in arguments[1]) {
              ++attributeLengthB;
            }
            //如果两个对象的属性数目不等，则两个对象也不等
            if (attributeLengthA != attributeLengthB)
              result = false;
            return result;
          }
          return arguments[0] == arguments[1];
        }
      },
      computed: {
        //vuex 分发用户信息
        user: function () {
          this.doctorId = this.$store.state.user;
          if(this.doctorId && this.yn ){
            //第一次有信息分发出来
            this.type=this.doctorId.userType;//记录当前用户角色 1：医生，2：专家 ，3：运营人员
            this.yn=false;
            this.pationListDateLast=[];//今日协诊列表清零
            this.pationListDateLast=[];//后续协诊列表清零
            //"分发用户信息"
            this.ApationeList(this.doctorId);//刷新列表
          }
          return this.doctorId
        },
        //分发未读消息
        tokingM: function () {
          var parm = this.$store.state.tookenM;
          if(parm){//如果有未读列表有更新
           //"未读消息渲染"
            parm;// 刷新未读列表数据
            for(let i in this.redList){//重置列表所有未读状态
              this.redList[i].red=false;
            }
            this.notRading(parm)// 刷新列表数据
          }
           return false
        },
        //分发上次聊天订单
        OderingData:function () {
         var data=this.Odering;
         //上次订单被更新";
          if(data==-100){
            this.aler=null;
          }else if(data){//存在更新就记录上次订单
            if(data.orderNo){
              this.aler=data;
            }
         }
         return 1
      },
      //分发聊天订单
        patientData:function () {
          var dat=this.$store.state.patientList;
           if(dat){//存在更新就刷新列表
             var a=JSON.stringify(dat);
             var b= JSON.stringify(this.dataListOrld);
             if(b==a){
                 return
             }
             this.dataListOrld=dat;
             this.pationListDateLastSize = 0;
             this.pationListDateSize = 0;
             this.pantioneData(dat)
           }
           return 1
        },
      ...mapState([
        'Odering'
      ]),
      },
    }
</script>



